<template>
  <!-- 用户信息页面 -->
  <div class="Profile_info">
    <Header GoBack="true">
      <span slot="profile_info">用户信息</span>
    </Header>
    <section class="profile-info">
      <section class="headportrait">
        <input
          type="file"
          class="profileinfopanel-upload file"
          name="file"
          accept="image/png, image/gif, image/jpeg"
          @change="update"
        />
        <h2>头像</h2>
        <div class="headportrait-div">
          <img :src="`https://elm.cangdu.org/img/${avatar}`" class="headportrait-div-top" />
          <span class="headportrait-div-bottom">
            <van-icon name="arrow" class="headportrait-div-bottom-inco" />
          </span>
        </div>
      </section>
      <router-link to="/profile/info/setusername" tag="section" class="headportrait headportraitwo">
        <h2>用户名</h2>
        <div class="headportrait-div">
          <p>{{this.$store.state.user.user_name}}</p>
          <span class="headportrait-div-bottom">
            <van-icon name="arrow" class="headportrait-div-bottom-inco" />
          </span>
        </div>
      </router-link>
      <router-link
        to="/profile/info/address"
        tag="section"
        class="headportrait headportraitwo headportraithree"
      >
        <h2>收货地址</h2>
        <div class="headportrait-div">
          <span class="headportrait-div-bottom">
            <van-icon name="arrow" class="headportrait-div-bottom-inco" />
          </span>
        </div>
      </router-link>
      <section class="bind-phone">账号绑定</section>
      <section class="headportrait headportraitwo">
        <h2>
          <img
            src="data:image/png;base64,"
            alt
            style="margin-right: 0.1rem; "
          />手机
        </h2>
        <div class="headportrait-div">
          <span class="headportrait-div-bottom">
            <van-icon name="arrow" class="headportrait-div-bottom-inco" />
          </span>
        </div>
      </section>
      <section class="bind-phone">安全设置</section>
      <router-link to="/forget" tag="section" class="headportrait headportraitwo">
        <h2>登录密码</h2>
        <div class="headportrait-div">
          <p>修改</p>
          <span class="headportrait-div-bottom">
            <van-icon name="arrow" class="headportrait-div-bottom-inco" />
          </span>
        </div>
      </router-link>
      <section class="danger">
        <van-button type="danger" class="button" @click="Danger()">退出登录</van-button>
      </section>
    </section>
    <router-view></router-view>
  </div>
</template>

<script>
import { Dialog } from "vant";
import axios from "axios";

// Dialog({ message: "提示" });
import Header from "../hfod/Header";
export default {
  data() {
    return {
      value: "",
      info: [], //页面数据
      datate: "",
      headPhoto: "",
      avatar: ""
    };
  },
  components: {
    Header
  },
  methods: {
    //退出登录
    Danger() {
      Dialog.confirm({
        title: "退出登录",
        message: "您确定要退出登录吗?"
      })
        .then(() => {
          // on confirm
          this.$store.commit("Danger");
          this.$router.go(-1);
        })
        .catch(() => {
          // on cancel
        });
    },
    //上传头像
    update(e) {
      let id = this.$store.state.user.user_id; //个人id
      let file = e.target.files[0]; //图片内容,事件,格式,名字

      let param = new FormData(); //创建form对象
      param.append("file", file, file.name); //通过append向form对象添加数据
      // param.append("chunk", "0"); //添加form表单中其他数据
      // console.log(param.get("file")); //FormData私有类对象，访问不到，可以通过get判断值是否传进去
      let config = {
        headers: { "Content-Type": "multipart/form-data" }
      }; //添加请求头
      axios
        .post(`http://elm.cangdu.org/eus/v1/users/${id}/avatar`, param, config)
        .then(response => {
          // console.log(response);

          if (response.data.status == 1) {
            //   this.Username(); //上传图片成功重新请求个人数据
            this.$store.commit("Avatar", response.data.image_path);
            this.avatar = response.data.image_path;
          }
        });
    }
  },
  mounted() {
    this.avatar = this.$store.state.user.avatar;
  }
};
</script>

<style lang="scss" scoped>
.Profile_info {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: #f5f5f5;
  z-index: 200;
  display: flex;
  flex-direction: column;

  .profile-info {
    flex-grow: 1;
    margin-top: 0.65rem;

    .headportrait {
      background: #fff;
      border-top: 1px solid #ddd;
      display: flex;
      justify-content: space-between;
      padding: 0.11rem 0.12rem;
      padding-right: 0.1rem;
      position: relative;
      .profileinfopanel-upload {
        position: absolute;
        top: 0;
        right: 0;
        width: 100%;
        left: 0;
        bottom: 0;
        opacity: 0;
      }
      p {
        display: block;
        margin-top: 0.15rem;
        color: #999;
        font-weight: 500;
      }
      h2 {
        display: flex;
        font-weight: normal;
        font-size: 0.18rem;
        color: #333;
        margin: auto 0;
      }
      .headportrait-div {
        display: flex;
        .headportrait-div-top {
          width: 0.56rem;
          height: 0.56rem;
          margin-right: 0.05rem;
          border-radius: 50%;
        }
        .headportrait-div-bottom {
          .headportrait-div-bottom-inco {
            line-height: 0.56rem;
            font-size: 0.25rem;
            color: rgb(216, 216, 216);
          }
        }
      }
    }
    .headportraitwo {
      margin: 0;
      padding: 0 0.14rem 0 0.1rem;
    }
    .headportraithree {
      border-bottom: 1px solid #ddd;
    }
    .danger {
      margin-top: 0.3rem;
      padding: 0 0.1rem;
      .button {
        width: 100%;
        border-radius: 0.05rem;
        font-size: 0.18rem;
      }
    }
  }
  .bind-phone {
    padding: 0.11rem;
    font-size: 0.14rem;
    color: #666;
  }
}
</style>